.emailList {
	flex: 1;
	overflow: scroll;
}

.emailList_settings {
	position: sticky;
	top: 0;
	display: flex;
	justify-content: space-between;
	border-bottom: 1px solid whitesmoke;
	background-color: white;
	z-index: 999;
	padding-right: 10px;
}

.emailList_sections {
	position: sticky;
	top: 0;
	display: flex;
	border-bottom: 1px solid whitesmoke;
	background-color: white;
	z-index: 999;
}
